.games__container {
  padding-top: size(120px);
  padding-bottom: size(120px);

  @include vp-1365 {
    padding-top: size(100px);
    padding-bottom: size(100px);
  }

  @include vp-767 {
    padding-top: size(70px);
    padding-bottom: size(70px);
  }
}

.games__banner {
  display: grid;
  grid-template-columns: size(500px) 1fr;
  justify-content: space-between;
  padding-bottom: size(125px);

  @include vp-1365 {
    grid-template-columns: size(294px) 1fr;
    padding-bottom: size(106px);
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    padding-bottom: size(75px);
  }

  h4,
  h5 {
    grid-column: 1/3;
    grid-row: 3/4;
    font-weight: 300;
    font-size: size(18px);
    line-height: size(24px);
    margin: 0;
    margin-top: size(-2px);

    @include vp-1365 {
      padding-bottom: 0;
    }

    @include vp-767 {
      font-size: size(16px);
      line-height: size(20px);
    }
  }

  h4 {
    @include vp-767 {
      padding-top: size(10px);
    }
  }

  img {
    width: 100%;
    height: 100%;
  }

  a {
    grid-column: 1/3;
    grid-row: 4/5;
    font-weight: 700;
    min-width: size(340px);

    @include vp-1365 {
      min-width: size(200px);
      height: 52px;
    }

    @include vp-767 {
      margin-top: size(15px);
    }
  }
}

.games__content {
  display: grid;
  grid-template-columns: 1fr size(93px);
  row-gap: size(22px);
  grid-template-rows: auto auto 1fr min-content;
  padding: size(81px) size(80px) size(83px);
  background-color: #ed0233;
  color: $color-default-white;

  @include vp-1365 {
    grid-template-columns: 1fr size(57px);
    padding: size(40px) size(47px) size(47px);
    row-gap: size(9px);
  }

  @include vp-767 {
    padding: size(40px);
    row-gap: size(5px);
    padding-bottom: size(43px);
  }

  h3 {
    grid-column: 1/2;
    grid-row: 1/3;
    font-family: "AlumniSans", "Arial", sans-serif;
    font-size: size(82px);
    font-weight: 600;
    line-height: size(76px);
    letter-spacing: size(2.46px);
    text-transform: uppercase;
    margin: 0;

    @include vp-1365 {
      font-size: size(48px);
      line-height: size(46px);
      letter-spacing: size(1.44px);
      width: min-content;
    }
  }

  p {
    font-size: size(14px);
    line-height: size(14px);
    margin: 0;

    @include vp-1365 {
      font-size: size(10px);
      line-height: size(10px);
    }
  }

  p:first-of-type {
    font-weight: 700;
    font-size: size(40px);
    line-height: size(40px);

    @include vp-1365 {
      font-size: size(24px);
      line-height: size(24px);
    }
  }
}

.games__data {
  grid-column: 2/3;
  grid-row: 1/2;
  min-height: size(75px);

  @include vp-1365 {
    min-height: size(48px);
  }
}

.games__data-holder,
.games__time-holder {
  position: relative;
  width: max-content;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  div {
    display: grid;
    grid-template-columns: min-content min-content;
    align-items: center;
    column-gap: size(7px);

    @include vp-1365 {
      padding-top: size(3px);
      column-gap: size(5px);
    }
  }

  p:first-of-type {
    grid-column: 1/2;
    grid-row: 1/3;
  }
}

.games__data-holder::before,
.games__time-holder::before {
  content: "";
  position: absolute;
  width: 97%;
  height: size(1px);
  background-color: $color-default-white;
  top: size(29px);

  @include vp-1365 {
    top: size(20px);
  }
}

.games__time {
  grid-column: 2/3;
  grid-row: 2/3;
  min-height: size(75px);

  @include vp-1365 {
    min-height: size(48px);
  }
}
